:root{--color-main: #f2f2f1;--color-main-light: rgba(242, 242, 241, 0.8);--color-main-lighter: rgba(242, 242, 241, 0.5);--color-oposite: #48484a;--color-accent: #ec6e4c;--color-accent-light: rgba(236, 110, 76, 0.8);--fs: 1rem;--fs-l: 1.953rem;--fs-xl: 2.441rem;--fw-thin: 100;--fw-light: 300;--fw-regular: 400;--box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5),
