-items:center}.Location .InputForm_label{width:100%;margin-bottom:1rem}.Location .InputForm_input{margin-bottom:1rem}.Location .InputForm_button{margin-bottom:1rem}@media (min-width: 768px){.Location .InputForm{flex-direction:row;justify-content:space-evenly}.Location .InputForm_label{width:auto}}.CurrentWeatherDetails_basic{display:flex;align-items:center;justify-content:center;padding-bottom:1rem}.CurrentWeatherDetails_icon{display:flex;align-items:center;justify-content:center;background-color:rgba(242, 242, 241, 0.5);background-color:var(--color-main-lighter);width:100px;height:100px;border-radius:50%;margin-right:1rem}.CurrentWeatherDetails_details{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100px;margin-left:1rem}.CurrentWeatherDetails_desc{font-weight:400;font-weight:var(--fw-regular);text-align:center}.CurrentWeatherDetails_temp{font-size:1.953rem;font-size:var(--fs-l)}.CurrentWeatherDetails_extended{list-style:none;padding:1rem 0;text-align:center}.CurrentWeatherDetails_extended li{padding:.5rem 0}

