/* Spinner from https://codepen.io/bernethe/pen/dorozd */
.Spinner {
  padding: 1rem 1rem 2rem;
  margin: 0 auto;
  color: var(--color-accent);
  text-align: center;
  width: fit-content;
  background: transparent;
  border-radius: 1rem;

  &_message {
    color: var(--color-main);
    margin-bottom: 2rem;
  }

  &_spinner {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    position: relative;
    opacity: 1;
    margin: 0 auto;

    &:before,
    &:after {
      content: '';
      border: 2px solid;
      border-radius: 50%;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0px;
    }
    &:before {
      transform: scale(1, 1);
      opacity: 1;
      -webkit-animation: spWaveBe 0.6s infinite linear;
      animation: spWaveBe 0.6s infinite linear;
    }
    &:after {
      transform: scale(0, 0);
      opacity: 0;
      -webkit-animation: spWaveAf 0.6s infinite linear;
      animation: spWaveAf 0.6s infinite linear;
    }
  }
}

@-webkit-keyframes spWaveAf {
  from {
    -webkit-transform: scale(0.5, 0.5);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1, 1);
    opacity: 1;
  }
}
@keyframes spWaveAf {
  from {
    transform: scale(0.5, 0.5);
    opacity: 0;
  }
  to {
    transform: scale(1, 1);
    opacity: 1;
  }
}
@-webkit-keyframes spWaveBe {
  from {
    -webkit-transform: scale(1, 1);
    opacity: 1;
  }
  to {
    -webkit-transform: scale(1.5, 1.5);
    opacity: 0;
  }
}
@keyframes spWaveBe {
  from {
    -webkit-transform: scale(1, 1);
    opacity: 1;
  }
  to {
    -webkit-transform: scale(1.5, 1.5);
    opacity: 0;
  }
}
